/*============================================================================
	#Menu
==============================================================================*/

ul.menu {
	margin: 0;
	padding: 0;
	list-style-type: none;
	display: flex;

	li {
	
		position: relative;

	}

	a {

		font-size: 16px;
		line-height: 1.4;
		text-decoration: none;
		font-weight: $medium;
		color: inherit;
		transition: color 0.4s cubic-bezier(0.165, 0.84, 0.44, 1);
		display: block;
		padding: 10px 0;
		
		&:hover {

			color: $colorPrimary;
		}
		
	}
	

	> li {

		margin: 0 16px;
		
	}

	> li.current-menu-item a {

		color: $colorPrimary;
	} 

	> li > a {}

}

ul.menu li > .sub-menu {

	position: absolute;
	left: -24px;
	margin: 0;
	padding: 0;
	list-style-type: none;
	background: white;
	padding: 10px 24px;
	box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.12);
	border-bottom-left-radius: 16px;
	border-bottom-right-radius: 16px;
	min-width: 200px;
	transform: translateX(24px);
	opacity: 0;
	visibility: hidden;
	transition: all 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);

	li {
		padding: 6px 0;
	}

	a {
		display: inline-block;
		padding: 0;
		font-size: 15px;
		position: relative;

		&::after {

			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			height: 1px;
			width: 100%;
			background: $colorGeneral;
			width: 0;
			transition: width 0.6s cubic-bezier(0.165, 0.84, 0.44, 1);
		}
	}
}

ul.menu > li:hover > a {

	color: $colorPrimary;
}
 
ul.menu li:hover > .sub-menu {

	visibility: visible;
	opacity: 1;
	transform: translateX(0);
}

ul.menu li > .sub-menu li a:hover {

	color: $colorGeneral;

	&::after {

		width: 100%;
	} 
}

ul.menu-categoties {

	> li {

		margin: 0 20px;
	}

	> li > a {

		position: relative;
		overflow: hidden;
		padding: 10px 0 13px;
		font-weight: $bold;
		color: $colorSecondary;

		&::after {

			content: '';
			position: absolute;
			left: 0;
			bottom: 0;

			height: 2px;
			width: 100%;
			
			background: $colorPrimary;
			transform: translateY(-4px);
			opacity: 0;
			transition: all .6s cubic-bezier(0.165, 0.84, 0.44, 1);
		}
	}

	> li.current-menu-item > a:hover::after {

		opacity: 0;
		transform: translateY(-4px);
	}
}

.menu-categoties > li:hover > a::after {
	
	transform: translateY(0);
	opacity: 1;
} 

